@import url('https://fonts.googleapis.com/css2?family=Courgette&display=swap');

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role='list'],
ol[role='list'] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
   scroll-behavior: auto;
  }
  
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: rgb(253, 241, 217);
  min-height: 100vh;
  min-width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(42, 39, 39);
}

h1 {
  font-family: 'Courgette', cursive;
  color: rgb(192, 55, 51);
  font-weight: 700;
  font-size: 4.4vh;
  letter-spacing: 0.1em;
  line-height: 4.4vh;
  -moz-text-fill-color: rgb(192, 55, 51);
  -webkit-text-fill-color: rgb(192, 55, 51);
  -moz-text-stroke-color: rgb(253, 241, 217);
  -webkit-text-stroke-color: rgb(253, 241, 217);
  -moz-text-stroke-width: 2px;  
  -webkit-text-stroke-width: 2px;
  paint-order: stroke fill;
  text-shadow:
    -1px -1px 0 rgb(42, 39, 39),
    -1px 1px 0 rgb(42, 39, 39),
    1px -1px 0 rgb(42, 39, 39),
    1px 1px 0 rgb(42, 39, 39),
    2px 2px 0 rgb(42, 39, 39),
    3px 3px 0 rgb(42, 39, 39),
    4px 4px 0 rgb(42, 39, 39);
}

h2 {
  font-family: 'Courgette', cursive;
  color: rgb(42, 39, 39);
  font-weight: 700;
  font-size: 3vh;
  text-shadow:
    1px 1px 0 rgb(151, 213, 208),
    2px 2px 0 rgb(151, 213, 208);
}

section {
  min-width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}

