.menuItem {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  min-width: 100vw;
  padding: 10px 0;
  box-sizing: border-box;
}

.menuItem > .MuiSvgIcon-root {
  margin-right: 20px;
  flex-grow: 1;
  font-size: 3em;
}

.menuItem > .numberCruncher {
  flex-grow: 1;
}

.menuItem > .togglable {
  flex-grow: 1;
}

.togglable {
  flex-grow: 1;
  padding: 5px 10px;
  margin: 5px 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  min-height: 4em;
}

button:focus {
  outline: none;
}

.numberCruncher {
  display: flex;
  flex-direction: column;
  background: rgb(151, 213, 208);
  padding: 5px;
  border-radius: 5px;
  min-width: 3em;
  font: cursive;
  font-weight: 700;
}

.numberCruncher button {
  border: none;
  cursor: pointer;
  background: none;
}

.price {
  position: fixed;
  bottom: 0;
  height: 5em;
  min-width: 100vw;
  background-color: rgb(253, 241, 217);
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 20px;
}

.App-link {
  color: rgb(192, 55, 51);
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
